import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect, useState } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFSelect } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
AirtimeSelInt.propTypes = {
  discount: PropTypes.object,
};
export default function AirtimeSelInt({ discount }) {
  const isMountedRef = useIsMountedRef();
  const [allvend, SelectAllVend] = useState([{ key: 'Habukhan1', value: '' }])
  const NewUserSchema = Yup.object().shape({
    mtn_vtu: Yup.string().required('MTN VTU VENDING REQUIRED'),
    airtel_vtu: Yup.string().required('AIRTEL VTU VENDING REQUIRED'),
    glo_vtu: Yup.string().required('GLO VTU VENDING REQUIRED'),
    mobile_vtu: Yup.string().required('9MOBILE VTU VENDING REQUIRED'),

    mtn_share: Yup.string().required('MTN SNS VENDING REQUIRED'),
    airtel_share: Yup.string().required('AIRTEL SNS VENDING REQUIRED'),
    glo_share: Yup.string().required('GLO SNS VENDING REQUIRED'),
    mobile_share: Yup.string().required('9MOBILE SNS VENDING REQUIRED'),
  });
  const defaultValues = useMemo(
    () => ({
      mtn_vtu: discount?.airtime?.mtn_vtu || '',
      airtel_vtu: discount?.airtime?.airtel_vtu || '',
      glo_vtu: discount?.airtime?.glo_vtu || '',
      mobile_vtu: discount?.airtime?.mobile_vtu || '',

      mtn_share: discount?.airtime?.mtn_share || '',
      airtel_share: discount?.airtime?.airtel_share || '',
      glo_share: discount?.airtime?.glo_share || '',
      mobile_share: discount?.airtime?.mobile_share || '',

    }),
    [discount]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount?.airtime !== undefined) {
      reset(defaultValues);
      SelectAllVend(discount?.habukhan_code)
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]);
  const {
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/edit/airtimesel/account/${AccessToken}/habukhan/secure`, data)
      swal('success', `Updated`, 'success');
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };


  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>

        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>

            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFSelect name="mtn_vtu" label="MTN VTU" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect name="airtel_vtu" label="AIRTEL VTU" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="glo_vtu" label="GLO VTU" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="mobile_vtu" label="9MOBILE VTU" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect name="mtn_share" label="MTN SNS" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect name="airtel_share" label="AIRTEL SNS" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="glo_share" label="GLO SNS" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="mobile_share" label="9MOBILE SNS" defaultValue="Habukhan1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
